<template>
  <mt-popup
    class="pup"
    v-if="init.key"
    v-model="init.key"
    :modal="false"
    position="bottom">
    <div class="box">
        <div class="b-header" @click.stop="rt()">
            <div>商品图片</div>
            <div>商品名</div>
            <div>销售价</div>
        </div>
        <div class="b-content" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
           <mt-loadmore :bottom-method="loadBottom" @bottom-status-change="handleBottomChange" :bottom-all-loaded="allLoaded" ref="loadmore">
                <ul class="page-loadmore-list">
                    <li v-if="list.length==0" class="page-loadmore-listitem">
                        <div>
                            暂无数据
                        </div>
                    </li>  
                    <li v-if="list.length>0" :class="defaultList.id==item.id?'active':''" data-id="item.id" v-for="(item,index) in list" :key="index" class="page-loadmore-listitem" @click="checkFn(item)">
                        <div>
                            <img :src="item.thumbnailSrc" alt="">
                        </div>
                        <div>
                           {{ item.name }}
                        </div>
                        <div>
                            {{item.price}}
                        </div>
                    </li>
                </ul>
                <div slot="bottom" class="mint-loadmore-bottom">
                <span v-show="bottomStatus !== 'loading'" :class="{ 'is-rotate': bottomStatus === 'drop' }">↑</span>
                <span v-show="bottomStatus === 'loading'" class="loading-icon">
                    <mt-spinner type="snake"></mt-spinner>
                </span>
                </div>
            </mt-loadmore>
        </div>
        <div class="b-footer">
            <button @click.stop="close">取消</button>
            <button @click.stop="update($event)">确定</button>
        </div>
    </div>
  </mt-popup>
</template>
<script>
import Exotic from '../../config.js';
import _$ from 'expose-loader?underscore!underscore';
// import { MessageBox } from 'mint-ui';
    export default{
        created(){
            let that=this;
            var param = {
                token:that.token,
                tradeAreaId: that.taid,
                isOnLine: 1,
                pageSize: 10,
                pageNumber: 1,
            };
            Exotic.ajaxData(that.token,Exotic.urlMap.product.list, param, 'get', function(res) {
                _$.each(res.data, function(item,i) {
                    item.thumbnailSrc = Exotic.imageUrl + item.thumbnail;
                    that.allGoods.push(item);
                });
                that.list=res.data;
                console.log(res);
                that.pageNumber = res.pageNumber;
                that.totalPages = res.totalPages;
            });
        },
        data(){
            return {
                //列表
                list: [
                    
                ],
                //选择的数据
                defaultList:{
                    id:null,
                    name:'',
                    price:null,
                    logo:''
                },
                allLoaded: false,//全部加加载完毕
                bottomStatus: '',
                wrapperHeight:0,//表格高度
                pageNumber:1,//当前页数
                totalPages:1,//总页数
                allGoods:[]
            }
        },
        props:['init','type','token','id','taid'],
        methods:{
            //点击选中高亮
            handleBottomChange(status) {
                this.bottomStatus = status;
            },
            //下拉加载
            loadBottom() {
                let that=this;
                setTimeout(() => {
                   if(this.pageNumber < this.totalPages) {
                        this.pageNumber++;
                        var param = {
                            tradeAreaId: this.taid,
                            isOnLine: 1,
                            pageSize: 10,
                            pageNumber: this.pageNumber,
                        };
                        //请求数据
                        Exotic.ajaxData(that.token,Exotic.urlMap.product.list, param, 'get',function(res) {
                            //添加数据到列表，合并图片路径
                            _$.each(res.data, function(item,i) {
                                item.thumbnailSrc = Exotic.imgUrl + item.thumbnail;
                                that.list.push(item);
                            });
                            that.pageNumber = res.pageNumber;
                            that.totalPages = res.totalPages;
                        });
                    }else {
                        this.allLoaded = true;
                    }
                    try{
                        this.$refs.loadmore.onBottomLoaded();
                    }catch(err){

                    }
                }, 1500);
            },
            //列表点击事件
            checkFn(item){
                this.defaultList.id=item.id;
                this.defaultList.name=item.name;
                this.defaultList.logo=item.thumbnailSrc;
                this.defaultList.thumbnail=item.thumbnail;
                this.defaultList.price=item.price;
            },
            //确定商品
            update(){
                if(this.defaultList.id!=null){
                    this.$emit('setlist',this.defaultList);
                    this.init.key=false;
                }else{
                    this.MintUI.MessageBox('提示', '请选择商品');
                }
            },
            rt(){
                return true;
            },
            close(){
                this.init.key=false;
            }
        },
        mounted() {
            this.wrapperHeight = document.documentElement.clientHeight - 30;
        }
    }
</script>
<style scoped lang="scss">
.pup{
    width:100%;
    background-color:rgba(0,0,0,.5);
    height:100%;
}
.box{
    width:86%;
    position: absolute;
    z-index: 66;
    top:10%;
    left:7%;
    height: 80%;
    border-radius: .3rem;
    background:#fff;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .b-header{
        height: 4rem;
        line-height:3.4rem;
        font-size:1.2rem;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        border-bottom:1px solid #eee;
        div{
            border-right:1px solid #eee;
        }
        div:nth-child(1){
            flex:2;
        }
        div:nth-child(2){
            flex:4;
        }
        div:nth-child(3){
            flex:2;
        }
    }
    .b-content{
        /*flex:1;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        margin-bottom:1rem;*/
        overflow-y: auto;
    }
    .b-footer{
        height: 5.2rem;
        line-height:5.2rem;
        display: flex;
        flex-direction: row;
        border-top:1px solid #eee;
        button{
            flex:1;
            text-align: center;
            background: transparent;
            border:0;
            padding:0;
            margin:0;
            font-size:1.2rem;
            outline: none;
        }
        button:active{
            background:#ddd;
        }
        button:first-child{
            border-radius:0 0 0 .3rem;
            border-right:1px solid #eee;
        }
        button:last-child{
            color:#e56fa6;
            border-radius:0 0 .3rem 0;
        }
    }
}
.page-loadmore-list li{
    height: 5rem;
    line-height: 5rem;
    border-bottom: 1px solid #eee;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    div{
        border-right:1px solid #eee;
        display: flex;
        justify-content: center;
        align-items: center;
        img{
            width:4.4rem;
            height: 4.4rem;
            display: block;
        }
    }
    div:nth-child(1){
        flex:2;
    }
    div:nth-child(2){
        flex:4;
    }
    div:nth-child(3){
        flex:2;
    }
}
.page-loadmore-list .active{
    border:#e56fa6 1px solid;
    background-color:#fff0f7;
}
.loading-icon{
    display: inline-block;
}
.check i{
    width:1.6rem;
    height:1.6rem;
    display: block;
    border:1px solid #ccc;
}
</style>



